import { motion, useAnimation } from "framer-motion";
import { useRef, useState, useEffect } from "react";
import { 
  FaHome, FaUser, FaBriefcase, FaAward, FaLaptopCode, FaServer, FaEnvelope, 
  FaExternalLinkAlt, FaCertificate, FaTrophy 
} from "react-icons/fa";

// ------------------- Datos de logros -------------------
const achievements = [
  {
    icon: <FaCertificate className="text-yellow-400 text-3xl" />,
    title: "Certificaci√≥n en Desarrollo Web Moderno",
    provider: "Udemy",
    year: "2024",
    description: "HTML, CSS, JavaScript, React y Node.js desde cero.",
    link: "https://www.udemy.com/certificate/UXXXXX/",
  },
  {
    icon: <FaTrophy className="text-blue-400 text-3xl" />,
    title: "Hackathon Nacional - 2¬∞ Lugar",
    provider: "Universidad de Chile",
    year: "2023",
    description: "Proyecto full stack con integraci√≥n de APIs y autenticaci√≥n avanzada.",
    link: "https://example.com/hackathon",
  },
  {
    icon: <FaAward className="text-purple-400 text-3xl" />,
    title: "Certificaci√≥n en Laravel Avanzado",
    provider: "Platzi",
    year: "2024",
    description: "Desarrollo backend con Laravel, API RESTful y optimizaci√≥n de consultas SQL.",
    link: "https://platzi.com/p/mardones/laravel-avanzado",
  },
  {
    icon: <FaCertificate className="text-green-400 text-3xl" />,
    title: "Curso de UI/UX Design Fundamentals",
    provider: "Coursera",
    year: "2023",
    description: "Principios de dise√±o centrado en el usuario y prototipado con Figma.",
    link: "https://coursera.org/certificate/UXUID1234",
  },
];

// ------------------- Dock Nav -------------------
const navItems = [
  { id: "inicio", icon: <FaHome />, label: "Inicio" },
  { id: "sobre-mi", icon: <FaUser />, label: "Sobre m√≠" },
  { id: "experiencia", icon: <FaBriefcase />, label: "Experiencia" },
  { id: "certificaciones", icon: <FaAward />, label: "Certificaciones y Logros" },
  { id: "tecnologias", icon: <FaLaptopCode />, label: "Tecnolog√≠as" },
  { id: "proyectos", icon: <FaServer />, label: "Proyectos" },
  { id: "contacto", icon: <FaEnvelope />, label: "Cont√°ctame" },
];

// ------------------- Componente Principal -------------------
export default function PortfolioPage() {
  // -------- Dock Nav --------
  const [activeNav, setActiveNav] = useState("inicio");

  useEffect(() => {
    const handleScroll = () => {
      const scrollPos = window.scrollY + window.innerHeight / 2;
      for (const item of navItems) {
        const el = document.getElementById(item.id);
        if (!el) continue;
        if (scrollPos >= el.offsetTop && scrollPos < el.offsetTop + el.offsetHeight) {
          setActiveNav(item.id);
          break;
        }
      }
    };
    window.addEventListener("scroll", handleScroll, { passive: true });
    handleScroll();
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const scrollToSection = (id: string) => {
    const el = document.getElementById(id);
    if (!el) return;
    window.scrollTo({ top: el.offsetTop, behavior: "smooth" });
    setActiveNav(id);
  };

  // -------- Carrusel Logros --------
  const containerRef = useRef<HTMLDivElement>(null);
  const controls = useAnimation();
  const [cardWidth, setCardWidth] = useState(350);
  const [cardHeight, setCardHeight] = useState(360);
  const gap = 24;

  useEffect(() => {
    const updateDimensions = () => {
      if (window.innerWidth < 768) { setCardWidth(300); setCardHeight(320); }
      else if (window.innerWidth < 1024) { setCardWidth(320); setCardHeight(340); }
      else { setCardWidth(350); setCardHeight(360); }
    };
    updateDimensions();
    window.addEventListener("resize", updateDimensions);
    return () => window.removeEventListener("resize", updateDimensions);
  }, []);

  // Scroll infinito inicial
  const startScroll = () => {
    if (!containerRef.current) return;
    const totalWidth = (cardWidth + gap) * achievements.length;
    const duration = totalWidth / 40;

    controls.start({
      x: [-0, -totalWidth],
      transition: {
        x: { repeat: Infinity, repeatType: "loop", duration: duration, ease: "linear" },
      },
    });
  };

  useEffect(() => { startScroll(); }, [cardWidth]);

  const loopItems = [...achievements, ...achievements];

  const handleHoverCard = (index: number) => {
    controls.stop();
    if (!containerRef.current) return;
    const containerWidth = containerRef.current.clientWidth;
    const targetScroll = index * (cardWidth + gap) - containerWidth / 2 + cardWidth / 2;
    containerRef.current.scrollTo({ left: targetScroll, behavior: "smooth" });
  };
  const handleLeaveCard = () => startScroll();

  return (
    <>
      {/* -------------------- Dock Nav -------------------- */}
      <motion.div
        initial={{ y: 100, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.6 }}
        className="fixed bottom-6 left-1/2 -translate-x-1/2 z-50 flex gap-4 sm:gap-6
                   bg-white/30 dark:bg-gray-800/50 backdrop-blur-lg
                   border border-white/10 rounded-full px-4 sm:px-8 py-3 shadow-xl overflow-x-auto"
      >
        {navItems.map((it) => {
          const isActive = activeNav === it.id;
          return (
            <motion.button
              key={it.id}
              onClick={() => scrollToSection(it.id)}
              whileHover={{ scale: 1.2 }}
              whileTap={{ scale: 0.9 }}
              className={`flex flex-col items-center text-[10px] sm:text-xs transition-all flex-shrink-0 ${
                isActive ? "text-cyan-400" : "text-gray-400 hover:text-cyan-300"
              }`}
            >
              <motion.div
                animate={{ scale: isActive ? 1.4 : 1 }}
                transition={{ type: "spring", stiffness: 200, damping: 10 }}
              >
                {it.icon}
              </motion.div>
              <span className="hidden sm:block mt-1 text-center">{it.label}</span>
            </motion.button>
          );
        })}
      </motion.div>

      {/* -------------------- Secciones -------------------- */}
      <section id="inicio" className="h-screen flex items-center justify-center bg-gray-900 text-white text-4xl font-bold">
        Inicio
      </section>

      <section id="sobre-mi" className="h-screen flex items-center justify-center bg-gray-800 text-white text-4xl font-bold">
        Sobre m√≠
      </section>

      <section id="experiencia" className="h-screen flex items-center justify-center bg-gray-700 text-white text-4xl font-bold">
        Experiencia
      </section>

      <section id="certificaciones" className="py-24 md:py-32">
        <h2 className="text-3xl md:text-4xl font-bold text-center mb-12">üèÜ Certificaciones y Logros</h2>
        <div className="relative overflow-x-hidden" style={{ height: `${cardHeight * 1.05}px` }}>
          <motion.div ref={containerRef} className="flex gap-6 relative z-0" animate={controls}>
            {loopItems.map((item, i) => (
              <motion.div
                key={i}
                whileHover={{ scale: 1.08, y: -10, boxShadow: "0px 15px 25px rgba(0,0,0,0.3)", zIndex: 20 }}
                onMouseEnter={() => handleHoverCard(i)}
                onMouseLeave={handleLeaveCard}
                className="flex-shrink-0 bg-gray-800/90 border border-white/10 p-6 rounded-2xl shadow-lg relative transition-transform duration-300"
                style={{ width: `${cardWidth}px`, height: `${cardHeight}px` }}
              >
                <div className="flex items-center gap-3 mb-4">
                  {item.icon}
                  <h3 className="text-lg font-semibold text-blue-400">{item.title}</h3>
                </div>
                <p className="text-gray-300 text-sm mb-1">
                  <span className="font-medium">{item.provider}</span> ‚Ä¢ {item.year}
                </p>
                <p className="text-gray-400 text-sm leading-relaxed mb-4">{item.description}</p>
                {item.link && (
                  <a
                    href={item.link}
                    target="_blank"
                    rel="noreferrer"
                    className="flex items-center gap-2 text-sm font-medium text-blue-400 hover:text-blue-300 border border-blue-400/30 px-3 py-2 rounded-lg bg-blue-500/10 hover:bg-blue-500/20 transition-colors duration-200"
                  >
                    Ver certificado <FaExternalLinkAlt size={12} />
                  </a>
                )}
              </motion.div>
            ))}
          </motion.div>
        </div>
      </section>

      <section id="tecnologias" className="h-screen flex items-center justify-center bg-gray-600 text-white text-4xl font-bold">
        Tecnolog√≠as
      </section>

      <section id="proyectos" className="h-screen flex items-center justify-center bg-gray-500 text-white text-4xl font-bold">
        Proyectos
      </section>

      <section id="contacto" className="h-screen flex items-center justify-center bg-gray-400 text-white text-4xl font-bold">
        Cont√°ctame
      </section>
    </>
  );
}
